



































































































































var user;
import myDrawer from '@/components/my-animation.vue';
export default {
	components: {
		myDrawer
	},
	onShow(){
		user = uni.getStorageSync('userLoginInfo');
	},
	data() {
		return {
			openAttr: false,
			checked: 1,
			radioItems: [{
					name: '微信支付',
					value: '1',
					icon: 'http://static.lamamuying.com/static/images/wx_icon.png',
					checked: 'true'
				},
				{
					name: '支付宝支付',
					icon: 'http://static.lamamuying.com/static/images/alipay_icon.png',
					value: '2'
				}
			],
		};
	},
	methods: {
		// 关闭规格弹窗
		closeAttr() {
			this.openAttr = false;
		},
		showAttr() {
			this.openAttr = true;
		},
		// 获取支付方式
		radioChange: function(e) {
			this.checked = e.detail.value;
			this.radioItems.forEach((radio)=>{
				if(radio.value == this.checked){
					radio.checked = true;
				}else{
					radio.checked = false;
				}
			});
		},
		// 请求支付接口
		goAppPay: function() {
			if(user.identity == 2){
				uni.showToast({
					title:'您已是辣妈Plus',
					icon:'none'
				})
				return;
			}
			if (this.checked == 1) {
				uni.showLoading({
					title:'加载中...'
				})
				//plus.nativeUI.alert("微信支付");
				uni.request({
					url: this.apiUrl + 'pay/appWechat',
					method: 'GET',
					data: {
						type: 'sale',
						uid: user.id,
						shop_id: user.shop.id
					},
					success: res => {
						if (res.data.code == 0) {
							uni.requestPayment({
								provider: 'wxpay',
								orderInfo: res.data.data, //微信、支付宝订单数据
								success: payRes => {
									user.identity = 2;
									uni.setStorageSync('userLoginInfo',user);
									uni.reLaunch({
										url:'./order_Plus'
									})
								},
								fail: payErr => {
									console.log('fail:' + JSON.stringify(payErr));
								}
							});
						}
					},
					fail: () => {},
					complete: () => {
						uni.hideLoading();
					}
				});
			} else {
				uni.showLoading({
					title:'加载中...'
				})
				uni.request({
					url: this.apiUrl + 'pay/appAlipay',
					method: 'GET',
					data: {
						//,order_trade_no: '2019040310550347797305',
						type: 'sale',
						uid: user.id,
						shop_id: user.shop.id
					},
					success: res => {
						if (res.data.code == 0) {
							uni.requestPayment({
								provider: 'alipay',
								orderInfo: res.data.data, //微信、支付宝订单数据
								success: payRes => {
									user.identity = 2;
									uni.setStorageSync('userLoginInfo',user);
									uni.reLaunch({
										url:'./order_Plus'
									})
								},
								fail: payErr => {
									this.checked  == 1;
									console.log('fail:' + JSON.stringify(payErr));
								}
							});
						}
					},
					fail: () => {},
					complete: () => {
						uni.hideLoading();
					}
				});
			}
		},
		// 小程序端微信支付
		wxpay() {
			if(user.identity == 2){
				uni.showToast({
					title:'您已是辣妈Plus',
					icon:'none'
				})
				return;
			}
			uni.request({
				url: this.apiUrl + 'pay/mpPay',
				method: 'GET',
				data: {
					type: 'order'
				},
				success: res => {
					if (res.data.code == 0) {
						// plus.nativeUI.alert(res)
						let payData = res.data;
						this.payParams = payData.data;
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: this.payParams.timeStamp,
							nonceStr: this.payParams.nonceStr,
							package: this.payParams.package,
							signType: this.payParams.signType,
							paySign: this.payParams.paySign,
							success: function(res) {
								console.log('success:' + JSON.stringify(res));
							},
							fail: function(err) {
								console.log('fail:' + JSON.stringify(err));
							}
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
	}
}
